import Service from '../abstract/service.js'

export default class UpcService extends Service {
  constructor() {
    super(process.env.VUE_APP_UPC_SERVICE_URL)
  }

  getZipCodes() {
    return this.http.get('/api/upc/zip')
  }

  getSettlements(data) {
    return this.http.post('/api/upc/city', data)
  }

  getStreetsWithTypes(data) {
    return this.http.post('/api/upc/street', data)
  }

  getHouseNumbers(data) {
    return this.http.post('/api/upc/house-number', data)
  }

  getFloors(data) {
    return this.http.post('/api/upc/floor', data)
  }

  getDoors(data) {
    return this.http.post('/api/upc/door', data)
  }

  getDefaultOffers() {
    return this.http.get('/api/upc/offers')
  }
  getOffers(data) {
    return this.http.post('/api/upc/offers', data)
  }
}
