const emailRegex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/

export default {
  required(localizedMessage) {
    return (value) => !!value || localizedMessage
  },

  mail(localizedMessage) {
    return (value) =>
      emailRegex.test(String(value).toLowerCase()) || localizedMessage
  },
}

