<template>
  <v-card class="pa-3 mt-5" tile dark id="callbackForm">
    <v-card-title>
      {{ $t(formContent.title) }}
    </v-card-title>
    <v-card-text>
      <v-form ref="callbackForm">
        <v-row>
          <v-col cols="12" md="6">
            <v-text-field
              outlined
              :label="$t(formContent.email)"
              v-model="formEmail"
              :rules="[
                required($t('common.validation.requiredValidationError')),
                mail($t('common.validation.emailValidationError')),
              ]"
              :disabled="isSending"
            >
            </v-text-field>
          </v-col>
          <v-col>
            <v-text-field
              outlined
              :label="$t(formContent.phoneNumber)"
              v-model="formPhoneNumber"
              :rules="[
                required($t('common.validation.requiredValidationError')),
              ]"
              :disabled="isSending"
            >
            </v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="6">
            <v-text-field
              outlined
              :label="$t(formContent.firstName)"
              v-model="formFirstName"
              :rules="[
                required($t('common.validation.requiredValidationError')),
              ]"
              :disabled="isSending"
            >
            </v-text-field>
          </v-col>
          <v-col>
            <v-text-field
              hide-details
              outlined
              :label="$t(formContent.lastName)"
              v-model="formLastName"
              :rules="[
                required($t('common.validation.requiredValidationError')),
              ]"
              :disabled="isSending"
            >
            </v-text-field>
          </v-col>
        </v-row>
      </v-form>
    </v-card-text>
    <v-card-actions>
      <v-btn
        tile
        @click="submit"
        :loading="isSending"
        :disabled="isSending"
        color="white"
        class="black--text ml-2"
        :aria-label="$t(formContent.buttonSend)"
        >{{ $t(formContent.buttonSend) }}
        <v-icon class="pl-3">mdi-send</v-icon>
      </v-btn>
    </v-card-actions>
    <v-alert class="ma-4" color="red accent-4" v-if="showErrorText" tile>
      {{ $t(loadingTexts.formError) }}
    </v-alert>
  </v-card>
</template>

<script>
import validations from '@/util/validations'
import { createHelpers } from 'vuex-map-fields'
import { content } from '@/content'
import { mapActions, mapGetters } from 'vuex'

const { mapFields } = createHelpers({
  getterType: 'callbackForm/getField',
  mutationType: 'callbackForm/updateField',
})

export default {
  data: () => ({
    formContent: content.pages.upc.form,
    loadingTexts: content.pages.upc.texts,
  }),
  methods: {
    ...validations,
    ...mapActions('callbackForm', [
      'sendForm',
      'setShowSuccessText',
      'setShowErrorText',
      'setForm',
    ]),
    submit() {
      if (this.$refs.callbackForm.validate()) {
        this.sendForm()
      }
    },
  },
  computed: {
    ...mapFields({
      formEmail: 'form.email',
      formPhoneNumber: 'form.phoneNumber',
      formFirstName: 'form.firstName',
      formLastName: 'form.lastName',
    }),
    ...mapGetters('callbackForm', [
      'isSending',
      'showSuccessText',
      'showErrorText',
    ]),
    ...mapGetters('user', ['email', 'firstName', 'lastName']),
  },
  mounted() {
    if (this.email) {
      let userData = {
        email: this.email,
        firstName: this.firstName,
        lastName: this.lastName,
      }
      this.setForm(userData)
    }
    this.$refs.callbackForm.resetValidation()
  },
}
</script>

<style scoped>
.v-text-field {
  border-radius: 0;
}
</style>
