<template>
  <div>
    <v-alert
      v-if="noService"
      color="red accent-4"
      dark
      prominent
      icon="warning"
    >
      {{ $t(addressSelector.noAddressText) }}
    </v-alert>
    <p class="display-1 mt-4">{{ $t(addressSelector.title) }}</p>
    <v-alert
      color="red accent-4"
      tile
      v-if="showErrorMsg"
      dark
      prominent
      icon="warning"
      >{{ $t(errorMessages.error400) }}</v-alert
    >
    <div class=" mt-0">
      <div class="hidden-sm-and-down">
        <p>
          <v-btn
            :disabled="isLoading"
            v-if="zipCode"
            class="mr-8"
            color="grey"
            tile
            @click="this.initAddress"
            >{{ $t(addressSelector.changeAddress) }}</v-btn
          >
          {{ zipCode }} {{ settlement }} {{ streetWithType }} {{ houseNumber }}
          {{ floor }} {{ door }}
        </p>
      </div>
      <div class="hidden-md-and-up">
        <v-btn
          v-if="zipCode"
          class="mb-5"
          color="grey"
          tile
          @click="this.initAddress"
          >{{ $t(addressSelector.changeAddress) }}</v-btn
        >
        <p>
          {{ zipCode }} {{ settlement }} {{ streetWithType }} {{ houseNumber }}
        </p>
        <p>{{ floor }} {{ door }}</p>
      </div>
    </div>
    <v-row justify="start" class="lajos">
      <v-col cols="12" sm="6" md="4" v-if="!zipCode">
        <v-autocomplete
          outlined
          :no-data-text="$t(addressSelector.noDataText)"
          append-icon="false"
          :items="availableZipCodes"
          @input="setZipCode"
          :disabled="isLoading"
          :label="$t(addressSelector.zipCode)"
        >
        </v-autocomplete>
      </v-col>
      <v-col
        cols="12"
        sm="6"
        md="4"
        v-if="
          !!availableSettlements.length &&
            availableSettlements.length > 1 &&
            settlement
        "
      >
        <v-autocomplete
          outlined
          :no-data-text="$t(addressSelector.noDataText)"
          append-icon="false"
          :items="availableSettlements"
          @input="setSettlement"
          :value="settlement"
          :label="$t(addressSelector.settlement)"
        >
        </v-autocomplete>
      </v-col>
      <v-col
        cols="12"
        sm="6"
        md="4"
        v-if="!!availableStreetsWithTypes.length && !streetWithType"
      >
        <v-autocomplete
          outlined
          :no-data-text="$t(addressSelector.noDataText)"
          append-icon="false"
          :items="availableStreetsWithTypes"
          @input="setStreetWithType"
          :label="$t(addressSelector.streetWithType)"
        >
        </v-autocomplete>
      </v-col>
      <v-col
        cols="12"
        sm="6"
        md="4"
        v-if="!!availableHouseNumbers.length && !houseNumber"
      >
        <v-autocomplete
          outlined
          :no-data-text="$t(addressSelector.noDataText)"
          append-icon="false"
          :items="availableHouseNumbers"
          @input="setHouseNumber"
          :label="$t(addressSelector.houseNumber)"
        >
        </v-autocomplete>
      </v-col>
      <v-col cols="12" sm="6" md="4" v-if="!!availableFloors.length && !floor">
        <v-autocomplete
          outlined
          :no-data-text="$t(addressSelector.noDataText)"
          append-icon="false"
          :items="availableFloors"
          @input="setFloor"
          :label="$t(addressSelector.floor)"
        >
        </v-autocomplete>
      </v-col>
      <v-col cols="12" sm="6" md="4" v-if="!!availableDoors.length && !door">
        <v-autocomplete
          outlined
          :no-data-text="$t(addressSelector.noDataText)"
          append-icon="false"
          :items="availableDoors"
          @input="setDoor"
          :label="$t(addressSelector.door)"
        >
        </v-autocomplete>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import { content } from '@/content'
export default {
  name: 'upc-address-selector',
  data: () => ({
    common: content.pages.upc,
    addressSelector: content.pages.upc.addressSelector,
    loadingTexts: content.pages.upc.texts,
    errorMessages: content.common.errorMessages,
  }),
  methods: {
    ...mapActions('upc', [
      'getZipCodes',
      'setZipCode',
      'setSettlement',
      'setStreetWithType',
      'setHouseNumber',
      'setFloor',
      'setDoor',
      'getOffers',
      'initAddress',
    ]),
  },
  computed: {
    ...mapGetters('upc', [
      'isLoading',
      'availableZipCodes',
      'zipCode',
      'availableSettlements',
      'settlement',
      'availableStreetsWithTypes',
      'streetWithType',
      'availableHouseNumbers',
      'houseNumber',
      'availableFloors',
      'floor',
      'availableDoors',
      'door',
      'offers',
      'noService',
      'showErrorMsg',
    ]),
    ...mapGetters('callbackForm', ['showSuccessText']),
  },
  created() {
    this.initAddress()
    this.getZipCodes()
  },
}
</script>

<style scoped>
.v-text-field--outlined,
.v-text-field--solo {
  border-radius: 0;
}
.lajos {
  min-height: 120px;
}
</style>
