<template>
  <v-container>
    <upc-address-selector> </upc-address-selector>

    <v-alert tile outlined v-if="showAddressText" class="mt-12">
      <p class="headline ma-0">{{ $t(addressSelector.addressText) }}</p>
    </v-alert>
    <v-alert tile outlined v-if="showDefaultAddressText" class="mt-12">
      <p class="headline ma-0">{{ $t(addressSelector.defaultAddressText) }}</p>
    </v-alert>
    <div v-if="offers.length !== 0">
      <v-row v-for="(offer, i) in offers" :key="i">
        <v-col>
          <upc-tariff-card :offer="offer" :template="cardTemplates[offer.key]">
          </upc-tariff-card>
        </v-col>
      </v-row>
    </div>
    <v-alert class="mt-5" color="green light-2" v-if="showSuccessText" tile>
      {{ $t(loadingTexts.formSuccess) }}
    </v-alert>
    <callback-form v-if="showForm"> </callback-form>
  </v-container>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import { content } from '@/content'
import CallbackForm from '../components/form/callback-form'
import UpcAddressSelector from '../components/card/upc-address-selector'

export default {
  components: { UpcAddressSelector, CallbackForm },
  data: () => ({
    common: content.pages.upc,
    addressSelector: content.pages.upc.addressSelector,
    cardTemplates: content.pages.upc.cardTemplates,
    loadingTexts: content.pages.upc.texts,
    errorMessages: content.common.errorMessages,
  }),
  methods: {
    ...mapActions('upc', ['getDefaultOffers']),
  },
  computed: {
    ...mapGetters('upc', [
      'isLoading',
      'offers',
      'showForm',
      'noService',
      'showErrorMsg',
      'showAddressText',
      'showDefaultAddressText',
    ]),
    ...mapGetters('callbackForm', ['showSuccessText']),
  },
  created() {
    this.getDefaultOffers()
  },
}
</script>
