const _state = (content) => () => ({
  isLoading: false,
  cookie: null,
  availableZipCodes: [],
  zipCode: null,
  availableSettlements: [],
  settlement: null,
  availableStreetsWithTypes: [],
  streetWithType: null,
  availableHouseNumbers: [],
  houseNumber: null,
  availableFloors: [],
  floor: null,
  availableDoors: [],
  door: null,
  offers: [],
  showForm: false,
  noService: false,
  showErrorMsg: false,
  showAddressText: false,
  showDefaultAddressText: false,
})

const _getters = {
  isLoading: (state) => state.isLoading,
  cookie: (state) => state.cookie,
  availableZipCodes: (state) => state.availableZipCodes,
  zipCode: (state) => state.zipCode,
  availableSettlements: (state) => state.availableSettlements,
  settlement: (state) => state.settlement,
  availableStreetsWithTypes: (state) => state.availableStreetsWithTypes,
  streetWithType: (state) => state.streetWithType,
  availableHouseNumbers: (state) => state.availableHouseNumbers,
  houseNumber: (state) => state.houseNumber,
  availableFloors: (state) => state.availableFloors,
  floor: (state) => state.floor,
  availableDoors: (state) => state.availableDoors,
  door: (state) => state.door,
  offers: (state) => state.offers,
  showForm: (state) => state.showForm,
  noService: (state) => state.noService,
  showErrorMsg: (state) => state.showErrorMsg,
  showAddressText: (state) => state.showAddressText,
  showDefaultAddressText: (state) => state.showDefaultAddressText,
}

const _mutations = {
  SET_IS_LOADING(state, payload) {
    state.isLoading = payload
  },
  SET_COOKIE(state, payload) {
    state.cookie = payload
  },
  SET_ZIPS(state, payload) {
    state.availableZipCodes = payload
  },
  SET_ZIP(state, payload) {
    state.zipCode = payload
  },
  SET_SETTLEMENTS(state, payload) {
    state.availableSettlements = payload
  },
  SET_SETTLEMENT(state, payload) {
    state.settlement = payload
  },
  SET_AVAILABLE_STREET_WITH_TYPES(state, payload) {
    state.availableStreetsWithTypes = payload
  },
  SET_STREET_WITH_TYPE(state, payload) {
    state.streetWithType = payload
  },
  SET_AVAILABLE_HOUSE_NUMBERS(state, payload) {
    state.availableHouseNumbers = payload
  },
  SET_HOUSE_NUMBER(state, payload) {
    state.houseNumber = payload
  },
  SET_AVAILABLE_FLOORS(state, payload) {
    state.availableFloors = payload
  },
  SET_FLOOR(state, payload) {
    state.floor = payload
  },
  SET_AVAILABLE_DOORS(state, payload) {
    state.availableDoors = payload
  },
  SET_DOOR(state, payload) {
    state.door = payload
  },
  SET_OFFERS(state, payload) {
    state.offers = payload
  },
  SET_SHOW_FORM(state, payload) {
    state.showForm = payload
  },
  SET_NO_SERVICE(state, payload) {
    state.noService = payload
  },
  SET_SHOW_ERROR_MSG(state, payload) {
    state.showErrorMsg = payload
  },
  SET_SHOW_ADDRESS_TEXT(state, payload) {
    state.showAddressText = payload
  },
  SET_SHOW_DEFAULT_ADDRESS_TEXT(state, payload) {
    state.showDefaultAddressText = payload
  },
  INIT_ADDRESS(state) {
    state.zipCode = null
    state.settlement = null
    state.availableSettlements = []
    state.streetWithType = null
    state.availableStreetsWithTypes = []
    state.houseNumber = null
    state.availableHouseNumbers = []
    state.floor = null
    state.availableFloors = []
    state.door = null
    state.availableDoors = []
    state.offers = []
  },
}

const _actions = {
  setIsLoading(context, payload) {
    context.commit('SET_IS_LOADING', payload)
  },
  setCookie(context, payload) {
    context.commit('SET_COOKIE', payload)
  },
  setZipCodes(context, payload) {
    context.commit('SET_ZIPS', payload)
  },
  setZipCode(context, payload) {
    context.commit('SET_ZIP', payload)
    context.dispatch('getSettlementByZip', payload)
  },
  setSettlements(context, payload) {
    context.commit('SET_SETTLEMENTS', payload)
    if (payload.length === 1) {
      context.commit('SET_SETTLEMENT', payload[0])
      context.dispatch('getStreetsByCity')
    }
  },
  setSettlement(context, payload) {
    context.commit('SET_SETTLEMENT', payload)
    context.dispatch('getStreetsByCity')
  },
  setAvailableStreetsWithTypes(context, payload) {
    context.commit('SET_AVAILABLE_STREET_WITH_TYPES', payload)
  },
  setStreetWithType(context, payload) {
    context.commit('SET_STREET_WITH_TYPE', payload)
    context.dispatch('getHouseNumbers')
  },
  setAvailableHouseNumbers(context, payload) {
    context.commit('SET_AVAILABLE_HOUSE_NUMBERS', payload)
  },
  setHouseNumber(context, payload) {
    context.commit('SET_HOUSE_NUMBER', payload)
    context.dispatch('getFloors')
  },
  setAvailableFloors(context, payload) {
    context.commit('SET_AVAILABLE_FLOORS', payload)
  },
  setFloor(context, payload) {
    context.commit('SET_FLOOR', payload)
    context.dispatch('getDoors')
  },
  setAvailableDoors(context, payload) {
    context.commit('SET_AVAILABLE_DOORS', payload)
  },
  setDoor(context, payload) {
    context.commit('SET_DOOR', payload)
    context.dispatch('getOffers')
  },
  setOffers(context, payload) {
    context.commit('SET_OFFERS', payload)
  },
  setShowForm(context, payload) {
    context.commit('SET_SHOW_FORM', payload)
  },
  setNoService(context, payload) {
    context.commit('SET_NO_SERVICE', payload)
  },
  setShowErrorMsg(context, payload) {
    context.commit('SET_SHOW_ERROR_MSG', payload)
  },
  setShowAddressText(context, payload) {
    context.commit('SET_SHOW_ADDRESS_TEXT', payload)
  },
  setShowDefaultAddressText(context, payload) {
    context.commit('SET_SHOW_DEFAULT_ADDRESS_TEXT', payload)
  },

  initAddress(context) {
    context.commit('INIT_ADDRESS')
    context.dispatch('setShowForm', false)
    context.dispatch('callbackForm/initForm', false, { root: true })
    context.dispatch('getDefaultOffers')
    context.dispatch('setShowAddressText', false)
  },

  async getZipCodes(context) {
    try {
      context.dispatch('setIsLoading', true)
      let res = await this.$api.upcService.getZipCodes()
      context.dispatch('setCookie', res.data.session)
      context.dispatch('setZipCodes', res.data.responseData.suggestions)
    } catch (e) {
      console.log(e)
      context.dispatch('setShowErrorMsg', true)
      setTimeout(() => {
        context.dispatch('setShowErrorMsg', false)
      }, 4000)
    } finally {
      context.dispatch('setIsLoading', false)
    }
  },

  async getSettlementByZip(context) {
    let data = {
      address: {
        zipCode: context.state.zipCode,
      },
    }
    try {
      context.dispatch('setIsLoading', true)
      let res = await this.$api.upcService.getSettlements(data)
      handleResponse(context, 'setSettlements', res.data.suggestions)
    } catch (e) {
      context.dispatch('setShowErrorMsg', true)
      setTimeout(() => {
        context.dispatch('setShowErrorMsg', false)
      }, 4000)
      console.log(e)
    } finally {
      context.dispatch('setIsLoading', false)
    }
  },

  async getStreetsByCity(context) {
    let data = {
      address: {
        zipCode: context.state.zipCode,
        settlementName: context.state.settlement,
      },
    }
    try {
      context.dispatch('setIsLoading', true)
      let res = await this.$api.upcService.getStreetsWithTypes(data)
      handleResponse(
        context,
        'setAvailableStreetsWithTypes',
        res.data.suggestions
      )
    } catch (e) {
      context.dispatch('setShowErrorMsg', true)
      setTimeout(() => {
        context.dispatch('setShowErrorMsg', false)
      }, 4000)
      console.log(e)
    } finally {
      context.dispatch('setIsLoading', false)
    }
  },

  async getHouseNumbers(context) {
    let data = {
      address: {
        zipCode: context.state.zipCode,
        settlementName: context.state.settlement,
        streetWithTypeName: context.state.streetWithType,
      },
    }
    try {
      context.dispatch('setIsLoading', true)
      let res = await this.$api.upcService.getHouseNumbers(data)
      handleResponse(context, 'setAvailableHouseNumbers', res.data.suggestions)
    } catch (e) {
      context.dispatch('setShowErrorMsg', true)
      setTimeout(() => {
        context.dispatch('setShowErrorMsg', false)
      }, 4000)
      console.log(e)
    } finally {
      context.dispatch('setIsLoading', false)
    }
  },

  async getFloors(context) {
    let data = {
      address: {
        zipCode: context.state.zipCode,
        settlementName: context.state.settlement,
        streetWithTypeName: context.state.streetWithType,
        houseNumber: context.state.houseNumber,
      },
    }
    try {
      context.dispatch('setIsLoading', true)
      let res = await this.$api.upcService.getFloors(data)
      handleResponse(context, 'setAvailableFloors', res.data.suggestions)
    } catch (e) {
      context.dispatch('setShowErrorMsg', true)
      setTimeout(() => {
        context.dispatch('setShowErrorMsg', false)
      }, 4000)
      console.log(e)
    } finally {
      context.dispatch('setIsLoading', false)
    }
  },

  async getDoors(context) {
    let data = {
      address: {
        zipCode: context.state.zipCode,
        settlementName: context.state.settlement,
        streetWithTypeName: context.state.streetWithType,
        houseNumber: context.state.houseNumber,
        floorWithTypeName: context.state.floor,
      },
    }
    try {
      context.dispatch('setIsLoading', true)
      let res = await this.$api.upcService.getDoors(data)
      handleResponse(context, 'setAvailableDoors', res.data.suggestions)
    } catch (e) {
      context.dispatch('setShowErrorMsg', true)
      setTimeout(() => {
        context.dispatch('setShowErrorMsg', false)
      }, 4000)
      console.log(e)
    } finally {
      context.dispatch('setIsLoading', false)
    }
  },

  async getDefaultOffers(context) {
    try {
      context.dispatch('setIsLoading', true)
      let res = await this.$api.upcService.getDefaultOffers()
      context.dispatch('setOffers', res.data.packages)
      context.dispatch('setShowDefaultAddressText', true)
    } catch (e) {
      context.dispatch('setShowErrorMsg', true)
      setTimeout(() => {
        context.dispatch('setShowErrorMsg', false)
      }, 4000)
      console.log(e)
    } finally {
      context.dispatch('setIsLoading', false)
    }
  },

  async getOffers(context) {
    let data = {
      defaultData: {
        division: 'B2B',
        address: {
          zipCode: context.state.zipCode,
          settlementName: context.state.settlement,
          streetWithTypeName: context.state.streetWithType,
          houseNumber: context.state.houseNumber,
          floorWithTypeName: context.state.floor,
          flat: context.state.flat,
        },
      },
      sections: [
        'DTVBASEBOX',
        'INTBASE',
        'MOBBASE',
        'MVOBASE',
        'PACKAGEBASE',
        'PACKAGEBASE',
        'SPECIAL_PACKAGEBASE',
        'TVBASE',
        'VOPBASE',
      ],
    }
    try {
      context.dispatch('setIsLoading', true)
      let res = await this.$api.upcService.getOffers(data)
      context.dispatch('setOffers', res.data.packages)
      context.dispatch('setShowDefaultAddressText', false)
      context.dispatch('setShowAddressText', true)
    } catch (e) {
      context.dispatch('setShowErrorMsg', true)
      setTimeout(() => {
        context.dispatch('setShowErrorMsg', false)
      }, 4000)
      console.log(e)
    } finally {
      context.dispatch('setIsLoading', false)
    }
  },
}

function handleResponse(context, action, responseData) {
  if (responseData === null) {
    context.dispatch('getOffers')
  } else if (responseData.length === 0) {
    console.log('no service')
    context.dispatch('setNoService', true)
    setTimeout(() => {
      context.dispatch('setNoService', false)
    }, 5000)
  } else {
    context.dispatch(action, responseData)
  }
}

export default (content) => ({
  namespaced: true,
  state: _state(content),
  getters: _getters,
  mutations: _mutations,
  actions: _actions,
})
