import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import About from '../views/About.vue'
import MobileTariff from '../views/MobileTariff.vue'

import RedTariffs from '../views/tariffs/RedTariffs.vue'
import GoTariffs from '../views/tariffs/GoTariffs.vue'
import YouTariffs from '../views/tariffs/YouTariffs.vue'
import TutiTariffs from '../views/tariffs/TutiTariffs.vue'
import Upc from '../views/Upc'
import Mva from '../views/Mva'
import MvaBilling from '../components/card/mva/mva-billing'
import MvaFamilyExtend from '../components/card/mva/mva-family-extend'
import MvaHappy from '../components/card/mva/mva-happy'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home,
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
  },
  {
    path: '/about',
    name: 'about',
    component: About,
  },
  {
    path: '/mobiltarifak',
    name: 'mobiletariff',
    component: MobileTariff,
    children: [
      {
        path: 'red',
        component: RedTariffs,
      },
      {
        path: 'go',
        component: GoTariffs,
      },
      {
        path: 'you',
        component: YouTariffs,
      },
      {
        path: 'kartyas',
        component: TutiTariffs,
      },
    ],
  },
  {
    path: '/upc',
    name: 'upc',
    component: Upc,
  },
  {
    path: '/mva',
    name: 'mva',
    component: Mva,
    children: [
      {
        path: 'happy',
        component: MvaHappy,
      },
      {
        path: 'family-extend',
        component: MvaFamilyExtend,
      },
      {
        path: 'billing',
        component: MvaBilling,
      },
    ],
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
})

export default router
