import { getField, updateField } from 'vuex-map-fields'
import CallbackForm from '../../model/callbackForm'

const _state = (content) => () => ({
  form: new CallbackForm(),
  isSending: false,
  showSuccessText: false,
  showErrorText: false,
})

const _getters = {
  getField,
  isSending: (state) => state.isSending,
  showSuccessText: (state) => state.showSuccessText,
  showErrorText: (state) => state.showErrorText,
}

const _mutations = {
  updateField,
  SET_IS_SENDING(state, payload) {
    state.isSending = payload
  },
  SET_SHOW_SUCCESS_TEXT(state, payload) {
    state.showSuccessText = payload
  },
  SET_SHOW_ERROR_TEXT(state, payload) {
    state.showErrorText = payload
  },
  INIT_FORM(state) {
    state.form = new CallbackForm()
  },
  SET_FORM(state, payload) {
    state.form.email = payload.email
    state.form.firstName = payload.firstName
    state.form.lastName = payload.lastName
  },
}

const _actions = {
  async sendForm(context) {
    context.dispatch('setIsSending', true)
    try {
      await this.$api.formService.saveForm(context.state.form)
      context.dispatch('setShowSuccessText', true)
      context.dispatch('upc/setShowForm', false, { root: true })
      context.dispatch('initForm')
      setTimeout(() => {
        context.dispatch('setShowSuccessText', false)
      }, 4000)
    } catch (e) {
      context.dispatch('setShowErrorText', true)
      setTimeout(() => {
        context.dispatch('setShowErrorText', false)
      }, 4000)
    } finally {
      context.dispatch('setIsSending', false)
    }
  },

  setIsSending(context, payload) {
    context.commit('SET_IS_SENDING', payload)
  },
  setShowSuccessText(context, payload) {
    context.commit('SET_SHOW_SUCCESS_TEXT', payload)
  },
  setShowErrorText(context, payload) {
    context.commit('SET_SHOW_ERROR_TEXT', payload)
  },
  initForm(context) {
    context.commit('INIT_FORM')
  },
  setForm(context, payload) {
    context.commit('SET_FORM', payload)
  },
}

export default (content) => ({
  namespaced: true,
  state: _state(content),
  getters: _getters,
  mutations: _mutations,
  actions: _actions,
})
