<template>
  <div class="tariff">
    <v-container>
      <slide-navigator :items="menu" :root="tariffsRoot" :selected="selected">
      </slide-navigator>

      <slide-swap>
        <router-view></router-view>
      </slide-swap>

      <v-divider></v-divider>

      <social-footer></social-footer>

      <expansion-footer></expansion-footer>
    </v-container>
  </div>
</template>

<script>
import { content } from '@/content'

export default {
  data: () => ({
    menu: content.pages.tariffs.common.menu,
    tariffsRoot: content.pages.tariffs.common.rootPath,
    selected: null,
  }),
  created() {
    this.selected = this.$route.params.tariff
  },
  watch: {
    $route(to, from) {
      this.selected = to.params.tariff
    },
  },
}
</script>

<style scoped></style>
