<template>
  <div>
    <v-tabs
      fixed-tabs
      show-arrows
      centered
      dark
      icons-and-text
      height="120"
      optional
      :value="selectedIndex"
    >
      <v-tabs-slider hidden></v-tabs-slider>

      <v-tab v-for="(item, i) in items" :key="i" @click="$nav(item.to)">
        {{ $t(item.name) }}
        <v-icon x-large>{{ item.icon.name }}</v-icon>
      </v-tab>
    </v-tabs>
  </div>
</template>

<script>
export default {
  props: ['items', 'root'],
  data: () => ({
    selectedIndex: null,
  }),
  created() {
    this.selectedIndex = this.items
      .map((item) => item.to.url)
      .indexOf(this.$route.path)
  },
}
</script>

<style scoped></style>
