export default class NavPlugin {
  static install(Vue, options) {
    Vue.prototype.$nav = function({ url, open }) {
      const baseUrl = 'https://www.vodafone.hu'
      let target
      if (open) {
        target = url.startsWith('http') ? url : baseUrl + url
        window.open(target, '_blank')
      } else {
        if (url.startsWith('http')) window.open(url)
        else this.$router.push(url)
      }
    }
  }
}
