import Vue from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify'
import i18n from './plugins/i18n'
import NavPlugin from './plugins/nav'
import IdlePlugin from 'lutzvue'
import carousel from 'vue-owl-carousel'
import VueScrollTo from 'vue-scrollto'

Vue.component('carousel', carousel)

// Components
const register = (descriptor) => {
  const name = (path) => path.split('/')[path.split('/').length - 1]
  const component = (path) => require('@/components/' + path + '.vue').default
  Vue.component(name(descriptor), component(descriptor))
}

var components = [
  'navbar-top',

  'footer/fixed-footer',
  'footer/social-footer',
  'footer/expansion-footer',

  'button/login-button',
  'button/logout-button',
  'button/cheat-button',
  'button/system-bar-button',

  'tile/ad-tile/ad-tile',
  'tile/menu-tile',

  'card/device-card',
  'card/ad-card',
  'card/tariff-card',
  'card/upc-tariff-card',
  'card/advantage-card',
  'card/video-card',
  'card/image-card',
  'card/expansion-tile',

  'transition/grow-appear',
  'transition/fade-appear-swap',
  'transition/slide-swap',

  'slide-navigator/slide-navigator',
]
components.forEach(register)

Vue.use(NavPlugin)
Vue.use(IdlePlugin)
Vue.use(VueScrollTo)

Vue.config.productionTip = false

new Vue({
  router,
  store,
  vuetify,
  i18n,
  render: (h) => h(App),
}).$mount('#app')
